import { getAllSaleBillApi} from "@/api/saleBill"
import {getSaleBillMaxCodeApi} from "@/api/saleBill"
import {addOneSaleBillApi} from "@/api/saleBill"
import {getSaleBillDetailApi} from "@/api/saleBill"
import {deleteOneSaleBillApi} from "@/api/saleBill"
import {updateOneSaleBillApi} from "@/api/saleBill"
import {getCartPriceTotal} from "@/store/calculator"
const state=()=>{
    return{
        saleBillList:[],
        saleBillDetail:[]
    }
};
const getters={
    
};
const mutations={
    getAllSaleBill(state,payload)
    {
        state.saleBillList=payload
    },
    getSaleBillDetail(state,payload)
    {
        state.saleBillDetail=payload
    }
};
const actions={
    async getAllSaleBill(context)
    {
        const payload=await getAllSaleBillApi()
        context.commit("getAllSaleBill",payload)
    },
    async getSaleBillDetail(context,saleBill_ID)
    {
        const payload=await getSaleBillDetailApi(saleBill_ID)
        context.commit("getSaleBillDetail",payload)
    },
    async getSaleBillCodeMax()
    {
        const payload=await getSaleBillMaxCodeApi();
        return payload[0]["saleBillCodeMax"]
    },
    async addOneSaleBill(context,payload)
    {
        payload["detail_SB"]=JSON.parse(localStorage.getItem("cart"))
        payload["priceTotal"]=getCartPriceTotal(payload["detail_SB"])
        await addOneSaleBillApi(payload)
        alert("Thêm hoá đơn thành công")
        context.dispatch("getAllSaleBill")
    },
    async deleteOneSaleBill(context,payload)
    {
        await deleteOneSaleBillApi(payload)
        context.dispatch("getAllSaleBill")
    },
    async updateOneSaleBill(context,payload)
    {
        payload["detail_SB"]=[]
        if(payload.saleBill_Status==0){
            payload.saleBill_Status=0
            await updateOneSaleBillApi(payload)
            context.dispatch("getAllSaleBill")
        }
        else
        {
            payload.saleBill_Status=1
            await updateOneSaleBillApi(payload)
            context.dispatch("getAllSaleBill")
        }
    }
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}