<template>
    <div  @click="isClose"  class="overlay">
        <div @click.stop class="appModalFood">
            <div class="appModalFood__header container-fluid">
                <div class="header__left">
                    <h4 class="appModalFood__header--title">Thông tin món ăn</h4>
                </div>
                <div class="header__right">
                    <div class="appModalFood__header--question">
                        <i class="fa-solid fa-question"></i>
                    </div>
                    <div @click="isClose" class="appModalFood__header--close">
                        <i class="fa-sharp fa-solid fa-xmark"></i>
                    </div>
                </div>
            </div>
            <div class="appModalFood__content container-fluid">
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="foodCode"><b class="text-danger">*</b> Mã món ăn</label>
                        <input type="text" class="form-control" id="foodCode" v-model="food.foodCode">
                    </div>
                    <div class="row__right">
                        <label class="title " for="foodName"><b class="text-danger">*</b> Tên món ăn</label>
                        <input type="text" class="form-control" id="foodName" v-model="food.foodName" >
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="foodImage">Ảnh</label>
                        <input type="File"  @change="handleImage($event)" class="form-control" id="foodImage" >
                    </div>
                    <div class="row__right">
                        <label class="title " for="foodSale">Gỉam giá</label>
                        <div class="d-flex">
                            <input type="text" class="form-control" id="foodSale" v-model.number="food.foodSale" >
                            <b class="pt-2">(%)</b>
                        </div>
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="foodPrice">Gía</label>
                        <input type="text" class="form-control" id="foodPrice" v-model.number="food.foodPrice">
                    </div>
                    <div class="row__right">
                        <label class="title " for="foodQuantity">Số lượng hiện có</label>
                        <input type="text" class="form-control" id="foodQuantity" v-model.number="food.foodQuantity" >
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="foodCategoryName">Thể loại món ăn</label>
                        <select v-model="valueSelected" class="form-control" >
                            <option  v-for="(foodCategory,index) in foodCategoryList" :key="index"
                             :value="foodCategory"
                             >
                                {{foodCategory.foodCategoryName}}
                            </option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="appModalFood__footer container-fluid">
                <button class="btn btn-secondary">
                    Hủy
                </button>
                <button @click="saveOneFood(food) & isClose()" class="btn btn-success btn--save">
                    Lưu thông tin
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import {createNamespacedHelpers} from "vuex"
const {mapState,mapActions} =createNamespacedHelpers("food")
export default {
    data(){
        return{
            food:{
                foodID:"05c10da5-16f2-4a76-baef-3239c65a1df0",
                foodCode:"",
                foodName:"",
                foodImage:"",
                foodPrice:0,
                foodSale:1,
                foodQuantity:1,
                foodCategoryID:"",
                foodCategoryName:"",
                createDay:"2001-09-12",
                createBy:"Đỗ Đình Tỉnh",
                modifiedDay:"2001-09-12",
                modifiedBy:"Đỗ Đình Tỉnh"
            },
            
        }
    },
    props:{
        isClose:{
            type:Function
        },
        foodData:{
            type:Object
        }
    },
    computed:{
        ...mapState({
            foodCategoryList:"foodCategoryList"
        }),
        valueSelected: {
            set(newValue) {
                this.food.foodCategoryID=newValue.foodCategoryID
                this.food.foodCategoryName=newValue.foodCategoryName
            }
        }
    },
    methods:{
        handleImage(e)
        {
            this.food.foodImage=e.target.files[0]                
        },
        saveOneFood(payload){
            if(this.foodData=="")
            {
                this.addOneFood(payload)
            }
            else
            {
                this.updateOneFood(payload)
            }
        },
        ...mapActions({
            getFoodCodeMax:"getFoodCodeMax",
            addOneFood:"addOneFood",
            updateOneFood:"updateOneFood",
        }),
    },
    created(){
        if(this.foodData=="")
        {
            this.getFoodCodeMax().then(result=>
                this.food.foodCode=result
            );
        }
        else
        {
            this.food={...this.foodData}
        }

    }
    
}
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
    display: flex;
    align-items: center;
    @if $val == 1 {
      justify-content: space-between;
    } @else if $val==2 {
      justify-content: center;
    } @else if $val==3 {
      justify-content: end;
    }
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #607D8B; 
  border-radius: 10px;
}
.overlay
{
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}
.appModalFood
{
    height: 500px;
    width: 500px;
    background-color: #ffffff;
    margin: 100px auto;
}
.appModalFood__header
{
    height: 70px;
    border:1px solid #EFF2F4;
    @include flex-container(1)
}
.appModalFood__header--question,.appModalFood__header--close
{
    cursor: pointer;
}
.header__left
{
    height: 70px;
    width: 50%;
    @include flex-container(0)
}
.header__right
{
    height: 70px;
    width: 55px;
    @include flex-container(1)
}
.appModalFood__header--question,
.appModalFood__header--close
{
    width: 24px;
    height: 24px;
    border: 1px solid #A0B1BA;
    border-radius: 50%;
    @include flex-container(2)
}
.appModalFood__content
{
    height: calc(500px - 140px);
    border: 1px solid #A0B1BA;
    overflow-y:auto;
    padding-top:14px ;
}
.content__row
{
    height: 70px;
    margin-bottom: 14px;
    @include flex-container(0)
}
.row__left,.row__right
{
    width: 50%;
    height: 70px;
    padding-left: 10px;
}
.content__row title
{
    height: 22px;
}
#foodSale
{
    width: 100px;
}
.appModalFood__footer
{
    height: 70px;
    @include flex-container(1)
}
</style>