import {getCartPriceTotal} from "@/store/calculator"

const state=()=>{
    return{
        bookingFoodList:[],
        priceTotalCart:0
    }
};
const getters={
    
};
const mutations={
    getCart(state)
    {
        state.bookingFoodList =JSON.parse(localStorage.getItem("cart"))
        if(state.bookingFoodList==null)
        {
            state.bookingFoodList=[]
        }
        else
        {
           state.priceTotalCart= getCartPriceTotal(state.bookingFoodList)
        }
    },
    
    addToCart(state,food)
    {
        let cart=localStorage.getItem("cart")
        if(cart == null)
        {
            state.bookingFoodList.push(food)
            localStorage.setItem("cart",JSON.stringify(state.bookingFoodList))
        }
        else
        {
            let dem=0;
            let ls= JSON.parse(cart)
            for (let index = 0; index < ls.length; index++) {
                if(ls[index].foodID==food.foodID)
                {
                    dem=1;
                    ls[index].bookingQuantity+=1
                    localStorage.setItem("cart",JSON.stringify(ls))
                    break;
                }
            }
            if(dem==0)
            {
                ls.push(food)
                localStorage.setItem("cart",JSON.stringify(ls))
            }
        }
        alert("Đã thêm món ăn vào giỏ hàng ")
    },
    updateCart(state,bookingFoodList )
    {
        localStorage.setItem("cart",JSON.stringify(bookingFoodList))
        this.commit("getCart")
    },
    deleteCart(state,foodID){
        // state.bookingFoodList =JSON.parse(localStorage.getItem("cart"))
        state.bookingFoodList=state.bookingFoodList.filter(food=>food.foodID!=foodID)
        localStorage.setItem("cart",JSON.stringify(state.bookingFoodList))
    },
    
};
const actions={
    
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}