import {getAllFoodApi} from "@/api/food"
import {getFoodMaxCodeApi} from "@/api/food"
import {addOneFoodApi} from "@/api/food"
import {updateOneFoodApi} from "@/api/food"
import {deleteOneFoodApi} from "@/api/food"
import {getAllFoodCategoryApi} from "@/api/foodCategory"

const state=()=>{
    return{
        foodList:[],
        foodCategoryList:[],
        foodDetail:{}
    }
};
const getters={
    
};
const mutations={
    getAllFood(state,payload)
    {
        state.foodList=payload
    },
    getAllFoodCategory(state,payload)
    {
        state.foodCategoryList=payload
    },
    getOneFood(state,payload)
    {
        state.foodDetail=payload
    }
};
const actions={
    async getAllFood(context)
    {
        const payload=await getAllFoodApi()
        context.commit("getAllFood",payload)
    },
    async getFoodCodeMax()
    {
        const payload=await getFoodMaxCodeApi();
        return payload[0]["foodCodeMax"]
    },
    async addOneFood(context,payload)
    {
        var bodyFormData=new FormData()
        bodyFormData.append("foodID",payload.foodID)
        bodyFormData.append("foodCode",payload.foodCode)
        bodyFormData.append("foodName",payload.foodName)
        bodyFormData.append("foodImage",payload.foodImage)
        bodyFormData.append("foodPrice",payload.foodPrice)
        bodyFormData.append("foodSale",payload.foodSale)
        bodyFormData.append("foodQuantity",payload.foodQuantity)
        bodyFormData.append("foodCategoryID",payload.foodCategoryID)
        bodyFormData.append("foodCategoryName",payload.foodCategoryName)
        bodyFormData.append("createDay",payload.createDay)
        bodyFormData.append("createBy",payload.createBy)
        bodyFormData.append("modifiedDay",payload.modifiedDay)
        bodyFormData.append("modifiedBy",payload.modifiedBy)
        await addOneFoodApi(bodyFormData)
        context.dispatch("getAllFood")
        
     },
    async updateOneFood(context,payload)
    {
        var bodyFormData=new FormData()
        bodyFormData.append("foodID",payload.foodID)
        bodyFormData.append("foodCode",payload.foodCode)
        bodyFormData.append("foodName",payload.foodName)
        bodyFormData.append("foodImage",payload.foodImage)
        bodyFormData.append("foodPrice",payload.foodPrice)
        bodyFormData.append("foodSale",payload.foodSale)
        bodyFormData.append("foodQuantity",payload.foodQuantity)
        bodyFormData.append("foodCategoryID",payload.foodCategoryID)
        bodyFormData.append("foodCategoryName",payload.foodCategoryName)
        bodyFormData.append("createDay",payload.createDay)
        bodyFormData.append("createBy",payload.createBy)
        bodyFormData.append("modifiedDay",payload.modifiedDay)
        bodyFormData.append("modifiedBy",payload.modifiedBy)
        await updateOneFoodApi(payload)
        context.dispatch("getAllFood")
        
    },
    async deleteOneFood(context,payload)
    {
        await deleteOneFoodApi(payload)
        context.dispatch("getAllFood")
    },
    async getAllFoodCategory(context)
    {
        const payload=await getAllFoodCategoryApi()
        context.commit("getAllFoodCategory",payload)
    },
    async getOneFood(context,payload)
    {
        var fl=await getAllFoodApi()
        var food=fl.find(e=>e.foodID==payload)
        context.commit("getOneFood",food)
    }  
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}