<template>
    <the-header/>

    <!-- Page Header Start -->
    <div class="container-fluid page-header mb-5 wow fadeIn" data-wow-delay="0.1s">
        <div class="container">
            <h1 class="display-3 mb-3 animated slideInDown">GIỎ HÀNG</h1>
            <nav aria-label="breadcrumb animated slideInDown">
                <ol class="breadcrumb mb-0">
                    <li class="breadcrumb-item"><a class="text-body" href="#">Trang chủ</a></li>
                    <li class="breadcrumb-item text-dark active fw-bolder" aria-current="page">Giỏ hàng</li>
                </ol>
            </nav>
        </div>
    </div>
    <!-- Page Header End -->

    <section class="h-100" style="background-color: #eee;">
  <div class="container h-100 py-5">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-10">

        <div class="d-flex justify-content-between align-items-center mb-4">
          <h3 class="fw-normal mb-0 text-black">Giỏ hàng của bạn</h3>
          <!-- <div>
            <p class="mb-0"><span class="text-muted">Sort by:</span> <a href="#!" class="text-body">price <i
                  class="fas fa-angle-down mt-1"></i></a></p>
          </div> -->
        </div>
        <div>
          
        </div>
        <div  v-for="(food,index) in bookingFoodList" :key="index" class="card rounded-3 mb-4">
          <div class="card-body p-4">
            <div class="row d-flex justify-content-between align-items-center">
              <div class="col-md-2 col-lg-2 col-xl-2">
                <img
                  :src="food.foodImage"
                  class="img-fluid rounded-3" alt="Cotton T-shirt">
              </div>
              <div class="col-md-3 col-lg-3 col-xl-3">
                <p class="lead fw-normal mb-2">{{food.foodName}}</p>
                <p><span class="text-muted me-3">{{formatMoney(food.foodPrice)}} </span>Sale <span class="text-muted">({{food.foodSale}}) </span>%</p>
              </div>
              <div class="col-md-3 col-lg-3 col-xl-2 d-flex">
                <button class="btn btn-link px-2"
                @click="food.bookingQuantity--"
                  >
                  <i class="fas fa-minus"></i>
                </button>

                <input id="form1"  name="quantity" type="number"
                  class="form-control form-control-sm" 
                  :value="food.bookingQuantity"
                   />

                <button class="btn btn-link px-2"
                   @click="food.bookingQuantity++"

                  >
                  <i class="fas fa-plus"></i>
                </button>
              </div>
              <div class="col-md-3 col-lg-2 col-xl-2 offset-lg-1">
                <h5 class="mb-0">
                    {{priceTotalFood(food.foodPrice,food.foodSale,food.bookingQuantity)}}
                </h5>
              </div>
              <div class="col-md-1 col-lg-1 col-xl-1 text-end">
                <button @click="deleteCart(food.foodID)" class="border-0 text-danger"><i class="fas fa-trash fa-lg"></i></button>
              </div>
            </div>
          </div>
        </div>

        <!-- <div class="card mb-4">
          <div class="card-body p-4 d-flex flex-row">
            <div class="form-outline flex-fill">
              <input type="text" id="form1" class="form-control form-control-lg" />
              <label class="form-label" for="form1">Discound code</label>
            </div>
            <button type="button" class="btn btn-outline-warning btn-lg ms-3">Apply</button>
          </div>
        </div> -->

        <div class="card">
          <div class="card-body d-flex justify-content-between">
            <button @click="updateCart(bookingFoodList)" type="button"  class="btn btn-warning btn-block btn-lg fw-bolder">CẬP NHẬT GIỎ HÀNG</button>
            <router-link to="/checkout"  type="button"  class="btn btn-success btn-block btn-lg fw-bolder">TIẾN HÀNH ĐẶT HÀNG</router-link>

          </div>
        </div>

      </div>
    </div>
  </div>
    </section>

    <the-footer/>
</template>

<script>
import TheHeader from '@/components/Client/TheHeader.vue'
import TheFooter from '@/components/Client/TheFooter.vue'
import {createNamespacedHelpers} from "vuex"
const {mapState,mapMutations} =createNamespacedHelpers("cart")
import {formatter} from "@/store/format"
import {getFoodPriceTotal} from "@/store/calculator"
export default {
    components: { 
        TheFooter ,
        TheHeader
    },
    
    computed:{
        ...mapState({
            bookingFoodList:"bookingFoodList"
        })
    },
    methods:{
      formatMoney(val)
      {
          return formatter.format(val)
      },
      priceTotalFood(foodPrice,foodSale,bookingQuantity)
      {
          var sum= getFoodPriceTotal(foodPrice,foodSale,bookingQuantity)
          return formatter.format(sum)
      },
        ...mapMutations({
            updateCart:"updateCart",
            deleteCart:"deleteCart"
        })
    },
    
}
</script>


<style scoped>

</style>