<template>
  <div  @click="isClose"  class="overlay">
        <div @click.stop class="appModalSaleBill">
            <div class="appModalSaleBill__header container-fluid">
                <div class="header__left">
                    <h4 class="appModalSaleBill__header--title">Thông tin hoá đơn bán</h4>
                </div>
                <div class="header__right">
                    <div class="appModalSaleBill__header--question">
                        <i class="fa-solid fa-question"></i>
                    </div>
                    <div @click="isClose" class="appModalSaleBill__header--close">
                        <i class="fa-sharp fa-solid fa-xmark"></i>
                    </div>
                </div>
            </div>
            <div class="appModalSaleBill__content container-fluid">
                <div class="content__row">
                        <label class="title " for="code"> Mã hoá đơn</label>
                        <input type="text" disabled class="form-control" id="code" v-model="saleBill.saleBill_Code">                    
                </div>
                <div class="content__row">
                        <label class="title " for="customerName"> Tên khách hàng</label>
                        <input type="text" class="form-control" id="customerName" v-model="saleBill.customerName">                    
                </div>
                <div class="content__row">
                        <label class="title " for="phoneNumber"><b ></b>Số điện thoại</label>
                        <input type="text" class="form-control" id="phoneNumber" v-model="saleBill.phoneNumber">                    
                </div>
                <div class="content__row">
                        <label class="title " for="customerAddress"><b ></b>Địa chỉ khách hàng</label>
                        <input type="text" class="form-control" id="customerAddress" v-model="saleBill.customerAddress">                    
                </div>
                <div class="content__row ">
                        <label class="title mb-2" for="saleBill_Status"><b ></b>Trạng thái hoá đơn</label>
                        <label   class="container-status ">
                            <input  type="checkbox" v-model="saleBill.saleBill_Status"  class="checkStatus" :value="1" >
                            <div  class="billStatus round"></div>
                        </label>
                </div>
                
                
            </div>
            <div class="appModalSaleBill__footer container-fluid">
                <button class="btn btn-secondary">
                    Hủy
                </button>
                <button @click="updateOneSaleBill(saleBill) & isClose()" class="btn btn-success btn--save">
                    Lưu thông tin
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import {createNamespacedHelpers} from "vuex"
const {mapActions} =createNamespacedHelpers("saleBill")
export default {
    data(){
        return{
            saleBill:{}
        }
    },
    props:{
        isClose:{
            type:Function
        },
        saleBillData:{
            type:Object
        }
    },
    methods:{
        ...mapActions({
            updateOneSaleBill:"updateOneSaleBill"
        })
        
    },
    created()
    {
        this.saleBill={...this.saleBillData}
    }
}
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
    display: flex;
    align-items: center;
    @if $val == 1 {
      justify-content: space-between;
    } @else if $val==2 {
      justify-content: center;
    } @else if $val==3 {
      justify-content: end;
    }
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #607D8B; 
  border-radius: 10px;
}
.overlay
{
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}
.appModalSaleBill
{
    height: 500px;
    width: 500px;
    background-color: #ffffff;
    margin: 100px auto;
}
.appModalSaleBill__header
{
    height: 70px;
    border:1px solid #EFF2F4;
    @include flex-container(1)
}
.appModalSaleBill__header--question,.appModalSaleBill__header--close
{
    cursor: pointer;
}
.header__left
{
    height: 70px;
    width: 50%;
    @include flex-container(0)
}
.header__right
{
    height: 70px;
    width: 55px;
    @include flex-container(1)
}
.appModalSaleBill__header--question,
.appModalSaleBill__header--close
{
    width: 24px;
    height: 24px;
    border: 1px solid #A0B1BA;
    border-radius: 50%;
    @include flex-container(2)
}
.appModalSaleBill__content
{
    height: calc(500px - 140px);
    border: 1px solid #A0B1BA;
    overflow-y:auto;
    padding-top:14px ;
}
.content__row
{
    height: 70px;
    margin-bottom: 14px;
    display: flex;
    flex-direction: column;
}
.container-status
{
    position: relative;
    width: 60px;
    height: 34px;
}
.checkStatus
{
    display: none;
    margin-left: 5px;
    margin-top: 5px;
}
.billStatus
{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    cursor: pointer;
    background-color: #ccc;
}
.billStatus::before
{
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
}
.checkStatus:checked + .billStatus 
{
    background-color: #2196F3;
}
.checkStatus:focus + .billStatus {
    box-shadow: 0 0 1px #2196F3;
}
.checkStatus:checked + .billStatus:before {
    transform: translateX(26px);
}
.billStatus.round {
    border-radius: 34px;
}
.billStatus.round:before {
    border-radius: 50%;
}
.appModalSaleBill__footer
{
    height: 70px;
    @include flex-container(1)
}

</style>