<template>
    <the-header/>

    <the-carousel/>

    <!-- About Start -->
    <div class="container-xxl py-5">
        <div class="container">
            <div class="row g-5 align-items-center">
                <div class="col-lg-6 wow fadeIn" data-wow-delay="0.1s">
                    <div class="about-img position-relative overflow-hidden p-5 pe-0">
                        <img class="img-fluid w-100" src="assets/img/about.jpg">
                    </div>
                </div>
                <div class="col-lg-6 wow fadeIn" data-wow-delay="0.5s">
                    <h1 class="display-5 mb-4">Sản phẩm Sạch - Ngon - Bổ dưỡng</h1>
                    <p class="mb-4">Chúng tôi luôn sẵ sàng phục khách hàng và đảm bảo ba tiêu chí </p>
                    <p><i class="fa fa-check text-primary me-3"></i>Đồ ăn được chế biến sạch sẽ </p>
                    <p><i class="fa fa-check text-primary me-3"></i>Đồ ăn đem lại hương vị tươi ngon  </p>
                    <p><i class="fa fa-check text-primary me-3"></i>Đồ ăn đảm bảo cung cấp các chất dinh dưỡng cần thiết</p>
                    <a class="btn btn-primary rounded-pill py-3 px-5 mt-3" href="">Đọc thêm</a>
                </div>
            </div>
        </div>
    </div>
    <!-- About End -->

    <!-- Product Start -->
    <div class="container-xxl py-5">
        <div class="container">
            <div class="row g-0 gx-5 align-items-end">
                <div class="col-lg-6">
                    <div class="section-header text-start mb-5 wow fadeInUp" data-wow-delay="0.1s" style="max-width: 500px;">
                        <h1 class="display-5 mb-3">Our Products</h1>
                        <!-- <p>Tempor ut dolore lorem kasd vero ipsum sit eirmod sit. Ipsum diam justo sed rebum vero dolor duo.</p> -->
                    </div>
                </div>
                <div class="col-lg-6 text-start text-lg-end wow slideInRight" data-wow-delay="0.1s">
                    <ul class="nav nav-pills d-inline-flex justify-content-end mb-5">
                        <li class="nav-item me-2">
                            <a  class="btn btn-outline-primary border-2 active me-1" data-bs-toggle="pill" href="#tab-1"
                                v-for="(foodCategory,index) in foodCategoryList" :key="index" 
                            >
                               {{foodCategory.foodCategoryName}}
                            </a>
                        </li>
                        
                    </ul>
                </div>
            </div>
            <div class="tab-content">
                <div id="tab-1" class="tab-pane fade show p-0 active">
                    <div class="row g-4">
                        <div v-for="(food,index) in foodList" :key="index" class="col-xl-4 col-lg-6 col-md-6 wow fadeInUp" data-wow-delay="0.1s">
                            <div class="product-item">
                                <div class="position-relative bg-light overflow-hidden">
                                    <img class="img-fluid w-100" :src="food.foodImage" alt="">
                                    <div class="bg-secondary rounded text-white position-absolute start-0 top-0 m-4 py-1 px-3">New</div>
                                </div>
                                <div class="text-center p-4">
                                    <a class="d-block h5 mb-2" href="">{{food.foodName}}</a>
                                    <span class="text-primary me-1">{{formatMoney(food.foodPrice)}}</span>
                                    <!-- <span class="text-body text-decoration-line-through">{{food.foodSale}}(%)</span> -->
                                </div>
                                <div class="d-flex border-top">
                                    <small class="w-50 text-center border-end py-2 me-5">
                                        <a class="text-body" @click="toFoodDetail(food.foodID)" ><i class="fa fa-eye text-primar me-2"></i>Chi tiết</a>
                                    </small>
                                    <small class="w-50 text-center py-2">
                                        <a class="text-body" href=""><i class="fa fa-shopping-bag text-primary me-2"></i>Thêm vào giỏ hàng</a>
                                    </small>
                                </div>
                            </div>
                        </div>
                        
                        <div class="col-12 text-center wow fadeInUp" data-wow-delay="0.1s">
                            <a class="btn btn-primary rounded-pill py-3 px-5" href="">Hiển thị thêm</a>
                        </div>
                    </div>
                </div>
                
                
            </div>
        </div>
    </div>
    <!-- Product End -->

    <!-- Firm Visit Start -->
    <div class="container-fluid bg-primary bg-icon mt-5 py-6">
        <div class="container">
            <div class="row g-5 align-items-center">
                <div class="col-md-7 wow fadeIn" data-wow-delay="0.1s">
                    <h1 class="display-5 text-white mb-3">Đến với chúng tôi</h1>
                    <p class="text-white mb-0">Chúng tôi cam kết sẽ đem lại những trải nghiệm tuyệt vời nhất cho khách hàng </p>
                </div>
                <div class="col-md-5 text-md-end wow fadeIn" data-wow-delay="0.5s">
                    <a class="btn btn-lg btn-secondary rounded-pill py-3 px-5" href="">Visit Now</a>
                </div>
            </div>
        </div>
    </div>
    <!-- Firm Visit End -->

    <home-blog/>

    <the-footer/>
</template>

<script>
import TheCarousel from './TheCarousel.vue'
import TheHeader from '../TheHeader.vue'
import TheFooter from '../TheFooter.vue'
import HomeBlog from './HomeBlog.vue'
import {formatter} from "@/store/format"
import {createNamespacedHelpers} from "vuex"
const {mapState,mapActions} =createNamespacedHelpers("food")
export default {
    components:{
        TheHeader,
        TheCarousel,
        TheFooter,
        HomeBlog
    },
    computed:{
        ...mapState({
            foodList:"foodList",
            foodCategoryList:"foodCategoryList"
        })
    },
    methods:{
        formatMoney(val)
        {
            return formatter.format(val)
        },
        toFoodDetail(val)
        {
            this.$router.push(`/foodDetail/${val}`)
        },
        ...mapActions({
            getAllFood:"getAllFood",
            getAllFoodCategory:"getAllFoodCategory"
        })
    },
    created(){
        this.getAllFood()
        this.getAllFoodCategory()
    }
}
</script>

