<template>
  <div class="food">
    <div class="food__title">
        <h3 class="title--name">Món ăn</h3>
        <button @click="OpenAppModal()" class="btn btn-outline-success title--add">
            Thêm món ăn
        </button>
    </div>
    <div class="food__data bg-light">
            <div class="data__container-1">
                <div class="record--quantity--page">
                    <i class="text-muted">Hiển thị :</i>
                    <select class="form-control">
                      <option selected>10</option>
                      <option value="1">25</option>
                      <option value="2">50</option>
                      <option value="3">100</option>
                    </select>
                    <i class="text-muted">bản ghi</i>
                </div>
                <div class="record--search">
                    <i class="text-muted">Tìm kiếm :</i>
                    <input type="text" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
                </div>
            </div>
            <div class="data__container-2 table-responsive">
                <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                                    <thead>
                                        <tr>
                                            <th>
                                                <input type="checkbox" aria-label="Checkbox for following text input">
                                            </th>
                                            <th>Tên món ăn</th>
                                            <th>Ảnh</th>
                                            <th>Gía</th>
                                            <th>Gỉam giá</th>
                                            <th>Số lượng hiện có</th>
                                            <th>Tên thể loại món ăn</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(food,index) in foodList" :key="index">
                                            <td>
                                                <div class="column--val">
                                                    <input type="checkbox" aria-label="Checkbox for following text input">
                                                </div>
                                            </td>
                                            
                                            <td>
                                                <div class="column--val">
                                                    {{food.foodName}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val" >
                                                    <img style="height:100px" :src="food.foodImage" alt="">
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{food.foodPrice}}
                                                </div>    
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{food.foodSale}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                        {{food.foodQuantity}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                      {{food.foodCategoryName}}
                                                </div>
                                            </td>
                                            
                                            <td class="feature-column">
                                                <div class="column--val">
                                                <button @click="OpenAppModal(food)" class="btn btn-primary btn-fix me-2">
                                                    Sửa
                                                 </button>
                                                 <button @click="deleteOneFood(food.foodID)" class="btn btn-danger btn-deleteOne">
                                                    Xóa
                                                 </button>
                                                </div>
                                            </td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
            </div>
            <div class="data__container-3">
                <div class="record--quantity--count text-muted">
                    <i>Hiển thị 1 to 10 of 12 records</i> 
                </div>
                <div class="record--pagination ">
                    <div class="page">Previous</div>
                    <div class="page">1</div>
                    <div class="page">2</div>
                    <div class="page">3</div>
                    <div class="page">4</div>
                    <div class="page">5</div>
                    <div class="page">6</div>
                    <div class="page">Next</div>
                </div>
            </div>
    </div>
  </div>
    <app-modal-food
     v-if="isOpen"
    :isClose="CloseAppModal"
    :foodData="foodData"
    />
</template>

<script>
import appModalFood from './AppModalFood.vue';
import { createNamespacedHelpers } from 'vuex';
const {mapState,mapActions} =createNamespacedHelpers("food")
export default {
    data(){
        return{
            isOpen:false,
            foodData:""
        }
    },
    components:{
        appModalFood
    },
    computed:{
        ...mapState({
            foodList:"foodList",
        })
    },
    methods:{
        OpenAppModal(payload){
            if(payload==null)
            {
                this.isOpen=true
                this.foodData=""
            }
            else
            {
                this.isOpen=true
                this.foodData=payload
            }
        },
        CloseAppModal(){
            this.isOpen=false
        },
        ...mapActions({
            getAllFood:"getAllFood",
            deleteOneFood:"deleteOneFood",
            getAllFoodCategory:"getAllFoodCategory"
        })
    },
    created(){
        this.getAllFood();
        this.getAllFoodCategory()
    }
};
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
  display: flex;
  align-items: center;
  @if $val == 1 {
    justify-content: space-between;
  } @else if $val==2 {
    justify-content: center;
  } @else if $val==3 {
    justify-content: end;
  }
}
.food__title
{
    height: 80px;
    @include flex-container(1)
}
.food__data
{
    padding: 0px 14px;
    border: 1px solid #DCF1D8;
    box-shadow:0 4px 10px 2px rgba(0, 0, 0, 0.2);
}
input[type="checkbox"]
{
    height: 16px;
    width: 16px;
}
input[type="checkbox"]:checked
{
    accent-color: green;
}
.data__container-1,.data__container-3
{
    height: 50px;
    @include flex-container(1)
}

.record--quantity--page
{
   width: 50%;
   @include flex-container(0)
}
.custom-select
{
   width: 70px;
}
.record--search
{
   width: 50%;
   @include flex-container(3)
}
.form-control
{
    width: 200px;
}
.feature-column
{
    @include flex-container(1)
}
.record--quantity--count
{
    width: 50%;
    @include flex-container(0)
}
.record--pagination
{
    width: 50%;
    @include flex-container(3)
}
.record--pagination .page
{
    border: 1px solid #9CD5B3;
    padding: 14px;
    height: 40px;
    cursor: pointer;
    @include flex-container(0)
}
.record--pagination .page:hover
{
    color: #ffffff;
    background-color: #067933;
}


</style>