import {createRouter,createWebHistory} from "vue-router"
import adminPage from "@/components/Admin/AdminPage"
import employeeData from "@/components/Admin/Employee/EmployeeData"
import foodCategoryData from "@/components/Admin/FoodCategory/FoodCategoryData"
import foodData from "@/components/Admin/Food/FoodData"
import saleBillData from "@/components/Admin/SaleBill/SaleBillData"
import homePage from "@/components/Client/Home/HomePage"
import foodDetail from "@/components/Client/Product/FoodDetail"
import cartPage from "@/components/Client/Cart/CartPage"
import checkout from "@/components/Client/Checkout/CheckOut"
import loginPage from "@/components/Admin/Login/LoginPage"
import signUp from "@/components/Admin/Login/SignUp"
const routes=[
  {path:"/AdminPage",component:adminPage,
    children: [
        {path:"/employee",component:employeeData},
        {path:"/foodCategory",component:foodCategoryData},
        {path:"/food",component:foodData},
        {path:"/saleBill",component:saleBillData}
        // { path: '/:pathMatch(.*)*',component :employeeData,alias:"/employee" }
    ],
  },
  {path:"/homePage",component:homePage},
  {path:"/foodDetail/:id",component:foodDetail},
  {path:"/cartPage",component:cartPage},
  {path:"/checkout",component:checkout},
  {path:"/login",component:loginPage},
  {path : "/signUp",component : signUp},
  { path: '/:pathMatch(.*)*',component :homePage,alias:"/homePage" }
]

const router=createRouter({
  history:createWebHistory(),
  routes,
  linkActiveClass:"active_page"
})

export default router