
//Tính tổng giá một món ăn
var getFoodPriceTotal = (foodPrice, foodSale, bookingQuantity) => {
    var totalSale = foodPrice - ((foodPrice * foodSale) / 100);
    var total = totalSale * bookingQuantity;
    return total
}

//Tính tổng giá toàn bộ giỏ hàng
var getCartPriceTotal = (bookingFoodList) => {
    let sum = 0;
    for (let i = 0; i < bookingFoodList.length; i++) {
        sum += getFoodPriceTotal(bookingFoodList[i].foodPrice, bookingFoodList[i].foodSale, bookingFoodList[i].bookingQuantity);
    }
    return sum

}

export {
    getFoodPriceTotal,
    getCartPriceTotal
}