<template>
    <div  @click="isClose"  class="overlay">
        <div @click.stop class="appModalEmployee">
            <div class="appModalEmployee__header container-fluid">
                <div class="header__left">
                    <h4 class="appModalEmployee__header--title">Thông tin nhân viên</h4>
                </div>
                <div class="header__right">
                    <div class="appModalEmployee__header--question">
                        <i class="fa-solid fa-question"></i>
                    </div>
                    <div @click="isClose" class="appModalEmployee__header--close">
                        <i class="fa-sharp fa-solid fa-xmark"></i>
                    </div>
                </div>
            </div>
            <div class="appModalEmployee__content container-fluid">
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="code"><b class="text-danger">*</b> Mã nhân viên</label>
                        <input type="text" class="form-control" id="code" v-model="employee.employeeCode">
                    </div>
                    <div class="row__right">
                        <label class="title " for="name"><b class="text-danger">*</b> Tên nhân viên</label>
                        <input type="text" class="form-control" id="name" v-model="employee.employeeName" >
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="sex">Giới tính</label>
                        <div class="content__row--sex">
                            <div class="form-check mr-2">
                                <input class="form-check-input" v-model="employee.employeeSex" value="0" type="radio" name="sex" id="sex"/>
                                <label class="form-check-label ml-1" for="sex"> Nam </label>
                            </div>
                            <div class="form-check mr-2">
                                <input class="form-check-input" v-model="employee.employeeSex" value="1" type="radio" name="sex" id="sex"/>
                                <label class="form-check-label ml-1" for="sex"> Nữ </label>
                            </div>
                            <div class="form-check">
                                <input class="form-check-input" v-model="employee.employeeSex" value="2" type="radio" name="sex" id="sex"/>
                                <label class="form-check-label ml-1" for="sex"> Khác</label>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="dateOfBirth">Ngày sinh</label>
                        <input type="date" class="form-control" id="dateOfBirth" v-model="employee.dateOfBirth">
                    </div>
                    <div class="row__right">
                        <label class="title " for="email">Email</label>
                        <input type="text" class="form-control" id="email" v-model="employee.employeeEmail">
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="numberPhone">Số điện thoại </label>
                        <input type="text" class="form-control" id="numberPhone" v-model="employee.phoneNumber">
                    </div>
                    <div class="row__right">
                        <label class="title " for="identityCard">Chứng minh nhân dân</label>
                        <input type="text" class="form-control" id="identityCard" v-model="employee.identityCard">
                    </div>
                </div>
                <div class="content__row">
                    <div class="row__left">
                        <label class="title " for="bankAccount">Tài khoản ngân hàng</label>
                        <input type="text" class="form-control" id="bankAccount" v-model="employee.bankAccount" >
                    </div>
                    <div class="row__right">
                        <label class="title " for="bankName">Tên ngân hàng</label>
                        <input type="text" class="form-control" id="bankName" v-model="employee.bankName">
                    </div>
                </div>
                <div class="content__row">
                        <label class="title " for="address">Địa chỉ</label>
                        <input type="text" class="form-control" id="address" v-model="employee.employeeAddress">            
                </div>
            </div>
            <div class="appModalEmployee__footer container-fluid">
                <button class="btn btn-secondary">
                    Hủy
                </button>
                <button @click="saveOneEmployee(employee) & isClose()" class="btn btn-success btn--save">
                    Lưu thông tin
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import {createNamespacedHelpers} from "vuex"
const {mapActions} =createNamespacedHelpers("employee")
export default {
    data(){
        return{
            employee:{
                employeeID:"b66582bd-5ef6-485d-be64-86c880d9137b",
                employeeCode:"",
                employeeName:"",
                employeeSex:0,
                employeeEmail:"",
                dateOfBirth:"",
                phoneNumber:"",
                identityCard:"",
                bankAccount:"",
                bankName:"",
                employeeAddress:"",
                createDay:"2001-09-12",
                createBy:"Đỗ Đình Tỉnh",
                modifiedDay:"2001-09-12",
                modifiedBy:"Đỗ Đình Tỉnh"
            },
        }
    },
    props:{
        isClose:{
            type:Function
        },
        employeeData:{
            type:Object
        }
    },
    
    methods:{
        saveOneEmployee(payload){
            if(this.employeeData=="")
            {
                this.addOneEmployee(payload)
            }
            else
            {
                this.updateOneEmployee(payload)
            }
        },
        ...mapActions({
            getEmployeeCodeMax:"getEmployeeCodeMax",
            addOneEmployee:"addOneEmployee",
            updateOneEmployee:"updateOneEmployee"
        }),
    },
    created(){
        if(this.employeeData=="")
        {
            this.getEmployeeCodeMax().then(result=>
                this.employee.employeeCode=result
            );
        }
        else
        {
            this.employee={...this.employeeData}

        }
        
    },
    
}
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
    display: flex;
    align-items: center;
    @if $val == 1 {
      justify-content: space-between;
    } @else if $val==2 {
      justify-content: center;
    } @else if $val==3 {
      justify-content: end;
    }
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #607D8B; 
  border-radius: 10px;
}
.overlay
{
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}
.appModalEmployee
{
    height: 500px;
    width: 500px;
    background-color: #ffffff;
    margin: 100px auto;
}
.appModalEmployee__header
{
    height: 70px;
    border:1px solid #EFF2F4;
    @include flex-container(1)
}
.appModalEmployee__header--question,.appModalEmployee__header--close
{
    cursor: pointer;
}
.header__left
{
    height: 70px;
    width: 50%;
    @include flex-container(0)
}
.header__right
{
    height: 70px;
    width: 55px;
    @include flex-container(1)
}
.appModalEmployee__header--question,
.appModalEmployee__header--close
{
    width: 24px;
    height: 24px;
    border: 1px solid #A0B1BA;
    border-radius: 50%;
    @include flex-container(2)
}
.appModalEmployee__content
{
    height: calc(500px - 140px);
    border: 1px solid #A0B1BA;
    overflow-y:auto;
    padding-top:14px ;
}
.content__row
{
    height: 70px;
    margin-bottom: 14px;
    @include flex-container(0)
}
.row__left,.row__right
{
    width: 50%;
    height: 70px;
    padding-left: 10px;
}
.content__row title
{
    height: 22px;
}
.content__row--sex
{
    @include flex-container(0)
}
#code
{
    width: 180px;
}
.form-check
{
    width: 70px;
}
#sex
{
    width: 20px;
    height: 20px;
}
#sex:checked
{
    accent-color: green;
}
#dateOfBirth
{
    width: 200px;
}
.appModalEmployee__footer
{
    height: 70px;
    @include flex-container(1)
}
</style>