import {getAllFoodCategoryApi} from "@/api/foodCategory"
import {getFoodCategoryMaxCodeApi} from "@/api/foodCategory"
import {addOneFoodCategoryApi} from "@/api/foodCategory"
import {updateOneFoodCategoryApi} from "@/api/foodCategory"
import {deleteOneFoodCategoryApi} from "@/api/foodCategory"

const state=()=>{
    return{
        foodCategoryList:[],
    }
};
const getters={
    
};
const mutations={
    getAllFoodCategory(state,payload)
    {
        state.foodCategoryList=payload
    }
    
};
const actions={
    async getAllFoodCategory(context)
    {
        const payload=await getAllFoodCategoryApi()
        context.commit("getAllFoodCategory",payload)
    },
    async getFoodCategoryCodeMax()
    {
        const payload=await getFoodCategoryMaxCodeApi();
        return payload[0]["foodCategoryCodeMax"]
    },
    async addOneFoodCategory(context,payload)
    {
        await addOneFoodCategoryApi(payload)
        context.dispatch("getAllFoodCategory")
    },
    async updateOneFoodCategory(context,payload)
    {
        await updateOneFoodCategoryApi(payload)
        context.dispatch("getAllFoodCategory")
    },
    async deleteOneFoodCategory(context,payload)
    {
        await deleteOneFoodCategoryApi(payload)
        context.dispatch("getAllFoodCategory")
    }
    
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}