//Hàm format giá trị sang dạng tiền tệ
var formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'VND'
});

//Hàm format giá trị thành giới tính
const filterGender=(value)=>{
    switch (value)
            {
                case 0 : {
                    return "Nam";
                }
                case 1 : {
                    return "Nữ"
                }
                default : {
                    return ''
                }
    }
}

//Hàm định dạng trạng thái hoá đơn bán
const formatStatus_SB=(value)=>{
    switch (value)
            {
                case 0 : {
                    return "Chưa giao hàng";
                }
                case 1 : {
                    return "Đã giao hàng"
                }
    }
}

export {
    filterGender,
    formatter,
    formatStatus_SB
}