<template>
  <div class="saleBill">
    <div class="saleBill__title">
        <h3 class="title--name">Hoá đơn bán</h3>
        <button @click="OpenAppModal()" class="btn btn-outline-success title--add">
            Thêm hoá đơn bán
        </button>
    </div>
    <div class="saleBill__data bg-light">
            <div class="data__container-1">
                <div class="record--quantity--page">
                    <i class="text-muted">Hiển thị :</i>
                    <select class="form-control">
                      <option selected>10</option>
                      <option value="1">25</option>
                      <option value="2">50</option>
                      <option value="3">100</option>
                    </select>
                    <i class="text-muted">bản ghi</i>
                </div>
                <div class="record--search">
                    <i class="text-muted">Tìm kiếm :</i>
                    <input type="text" class="form-control" >
                </div>
            </div>
            <div class="data__container-2 table-responsive">
                <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                                    <thead>
                                        <tr>
                                            <th>
                                                <input type="checkbox" aria-label="Checkbox for following text input">
                                            </th>
                                            <th>Mã hoá đơn</th>
                                            <th>Tên khách hàng</th>
                                            <th>Số điện thoại</th>
                                            <th>Địa chỉ</th>
                                            <th>Trạng thái hoá đơn</th>
                                            <th>Tổng tiền</th>
                                            <th></th>
                                            
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr @dblclick="openSaleBillDetail(sb.saleBill_ID)" v-for="(sb,index) in saleBillList" :key="index">
                                            <td>
                                                <div class="column--val">
                                                    <input type="checkbox" aria-label="Checkbox for following text input">
                                                </div>
                                            </td>
                                            
                                            <td>
                                                <div class="column--val">
                                                    {{sb.saleBill_Code}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val" >
                                                    {{sb.customerName}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{sb.phoneNumber}}
                                                </div>    
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{sb.customerAddress}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{formatStatus_SB(sb.saleBill_Status)}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{formatMoney(sb.priceTotal)}}
                                                </div>
                                            </td>
                                            
                                            <td class="feature-column">
                                                <div class="column--val">
                                                <button @click="OpenAppModal(sb)" class="btn btn-primary btn-fix me-2">
                                                    Sửa
                                                 </button>
                                                 <button @click="deleteOneSaleBill(sb.saleBill_ID)" class="btn btn-danger btn-deleteOne">
                                                    Xóa
                                                 </button>
                                                </div>
                                            </td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
            </div>
            <div class="data__container-3">
                <div class="record--quantity--count text-muted">
                    <i>Hiển thị 1 to 10 of 12 records</i> 
                </div>
                <div class="record--pagination ">
                    <div class="page">Previous</div>
                    <div class="page">1</div>
                    <div class="page">2</div>
                    <div class="page">3</div>
                    <div class="page">4</div>
                    <div class="page">5</div>
                    <div class="page">6</div>
                    <div class="page">Next</div>
                </div>
            </div>
    </div>
  </div>
    
    <app-modal-sale-bill  
    v-if="isOpenAppModal"
    :isClose="closeAppModal"
    :saleBillData="saleBillData"
    />

    <sale-bill-detail
    v-if="isOpenDetail"
    :isClose="closeAppModal"

    />
</template>

<script>
import saleBillDetail from "./SaleBillDetail.vue"
import {formatter,formatStatus_SB} from "@/store/format"
import { createNamespacedHelpers } from 'vuex';
import AppModalSaleBill from './AppModalSaleBill.vue';
const {mapState,mapActions} =createNamespacedHelpers("saleBill")
export default {
    data(){
        return{
            isOpenDetail:false,
            isOpenAppModal:false,
            saleBillData:{}
        }
    },
    components:{
        saleBillDetail,
        AppModalSaleBill
    },
    computed:{
        ...mapState({
            saleBillList:"saleBillList"
        })
    },
    methods:{
        openSaleBillDetail(payload)
        {
            this.isOpenDetail=true
            this.getSaleBillDetail(payload)
        },
        closeAppModal()
        {
            this.isOpenDetail=false
            this.isOpenAppModal=false
        },
        formatStatus_SB(val)
        {
            return formatStatus_SB(val)
        },
        OpenAppModal(val)
        {
            this.isOpenAppModal=true
            this.saleBillData=val
        },
        formatMoney(val) {
            return formatter.format(val);
        },
        
        ...mapActions({
            getAllSaleBill:"getAllSaleBill",
            deleteOneSaleBill:"deleteOneSaleBill",
            getSaleBillDetail:"getSaleBillDetail"
        })
    },
    created(){
        this.getAllSaleBill();
    }
};
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
  display: flex;
  align-items: center;
  @if $val == 1 {
    justify-content: space-between;
  } @else if $val==2 {
    justify-content: center;
  } @else if $val==3 {
    justify-content: end;
  }
}
.saleBill__title
{
    height: 80px;
    @include flex-container(1)
}
.saleBill__data
{
    padding: 0px 14px;
    border: 1px solid #DCF1D8;
    box-shadow:0 4px 10px 2px rgba(0, 0, 0, 0.2);
}
input[type="checkbox"]
{
    height: 16px;
    width: 16px;
}
input[type="checkbox"]:checked
{
    accent-color: green;
}
.data__container-1,.data__container-3
{
    height: 50px;
    @include flex-container(1)
}

.record--quantity--page
{
   width: 50%;
   @include flex-container(0)
}
.custom-select
{
   width: 70px;
}
.record--search
{
   width: 50%;
   @include flex-container(3)
}
.form-control
{
    width: 200px;
}
.feature-column
{
    @include flex-container(1)
}
.record--quantity--count
{
    width: 50%;
    @include flex-container(0)
}
.record--pagination
{
    width: 50%;
    @include flex-container(3)
}
.record--pagination .page
{
    border: 1px solid #9CD5B3;
    padding: 14px;
    height: 40px;
    cursor: pointer;
    @include flex-container(0)
}
.record--pagination .page:hover
{
    color: #ffffff;
    background-color: #067933;
}

</style>