import axios from 'axios';

//Lấy tất cả món ăn
export const getAllFoodApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/food"
    })
    return res.data;
};

//Lấy mã món ăn lớn nhất
export const getFoodMaxCodeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/food/getFoodCodeMax"
    })
    return res.data;
};

//Thêm một món ăn
export const addOneFoodApi=async (food)=>{
    const res=await axios({
        method:"POST",
        url:"https://localhost:44363/api/food",
        data:food,
        headers: { "Content-Type": "multipart/form-data" },
    })
    return res.data
};

//Xóa một món ăn
export const deleteOneFoodApi=async (foodID)=>{
    const res=await axios({
        method:"DELETE",
        url:"https://localhost:44363/api/food/"+`${foodID}`,
    })
    return res.data;
};

//Cập nhật một thể loại món ăn
export const updateOneFoodApi=async (food)=>{
    const res=await axios({
        method:"PUT",
        url:"https://localhost:44363/api/food",
        data:food,
        headers: { "Content-Type": "multipart/form-data" },
    })
    return res.data;
};

//Lấy tất cả thể loại món ăn
export const getAllFoodCategoryApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/foodCategory"
    })
    return res.data;
};