import { deleteOneEmployeeApi, getAllEmployeeApi, updateOneEmployeeApi } from "@/api/employee"
import {getEmployeeMaxCodeApi} from "@/api/employee"
import {addOneEmployeeApi} from "@/api/employee"
const state=()=>{
    return{
        employeeList:[],
    }
};
const getters={
    
};
const mutations={
    getAllEmployee(state,payload)
    {
        state.employeeList=payload
    },
    
    
};
const actions={
    async getAllEmployee(context)
    {
        const payload=await getAllEmployeeApi();
        context.commit("getAllEmployee",payload)
    },
    async getEmployeeCodeMax()
    {
        const payload=await getEmployeeMaxCodeApi();
        return payload[0]["employeeCodeMax"]
    },
    async addOneEmployee(context,payload){
        await addOneEmployeeApi(payload);
        context.dispatch("getAllEmployee")
    },
    async updateOneEmployee(context,payload)
    {
        await updateOneEmployeeApi(payload)
        context.dispatch("getAllEmployee")
    },
    async deleteOneEmployee(context,payload)
    {
        await deleteOneEmployeeApi(payload)
        context.dispatch("getAllEmployee")
    }
    
}
export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}