<template>
    <div  @click="isClose"  class="overlay">
        <div @click.stop class="appModalFoodCategory">
            <div class="appModalFoodCategory__header container-fluid">
                <div class="header__left">
                    <h4 class="appModalFoodCategory__header--title">Thông tin nhóm món ăn</h4>
                </div>
                <div class="header__right">
                    <div class="appModalFoodCategory__header--question">
                        <i class="fa-solid fa-question"></i>
                    </div>
                    <div @click="isClose" class="appModalFoodCategory__header--close">
                        <i class="fa-sharp fa-solid fa-xmark"></i>
                    </div>
                </div>
            </div>
            <div class="appModalFoodCategory__content container-fluid">
                <div class="content__row">
                        <label class="title " for="code"><b class="text-danger">*</b> Mã nhóm món ăn</label>
                        <input type="text" class="form-control" id="code" v-model="foodCategory.foodCategoryCode">                    
                </div>
                <div class="content__row">
                        <label class="title " for="code"><b class="text-danger">*</b> Tên nhóm món ăn</label>
                        <input type="text" class="form-control" id="code" v-model="foodCategory.foodCategoryName">                    
                </div>
            </div>
            <div class="appModalFoodCategory__footer container-fluid">
                <button class="btn btn-secondary">
                    Hủy
                </button>
                <button @click="saveOneFoodCategory(foodCategory) & isClose()" class="btn btn-success btn--save">
                    Lưu thông tin
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import {createNamespacedHelpers} from "vuex"
const {mapActions} =createNamespacedHelpers("foodCategory")
export default {
    data(){
        return{
            foodCategory:{
                foodCategoryID:"b66582bd-5ef6-485d-be64-86c880d9137b",
                foodCategoryCode:"",
                foodCategoryName:"",
                createDay:"2001-09-12",
                createBy:"Đỗ Đình Tỉnh",
                modifiedDay:"2001-09-12",
                modifiedBy:"Đỗ Đình Tỉnh"
            },
        }
    },
    props:{
        isClose:{
            type:Function
        },
        foodCategoryData:{
            type:Object
        }
    },
    
    methods:{
        saveOneFoodCategory(payload){
            if(this.foodCategoryData=="")
            {
                this.addOneFoodCategory(payload)
            }
            else
            {
                this.updateOneFoodCategory(payload)
            }
        },
        ...mapActions({
            getFoodCategoryCodeMax:"getFoodCategoryCodeMax",
            addOneFoodCategory:"addOneFoodCategory",
            updateOneFoodCategory:"updateOneFoodCategory"
        }),
    },
    created(){
        if(this.foodCategoryData=="")
        {
            this.getFoodCategoryCodeMax().then(result=>
                this.foodCategory.foodCategoryCode=result
            );
        }
        else
        {
            this.foodCategory={...this.foodCategoryData}

        }
        
    },
    
}
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
    display: flex;
    align-items: center;
    @if $val == 1 {
      justify-content: space-between;
    } @else if $val==2 {
      justify-content: center;
    } @else if $val==3 {
      justify-content: end;
    }
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #607D8B; 
  border-radius: 10px;
}
.overlay
{
    position: fixed;
    z-index: 10;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}
.appModalFoodCategory
{
    height: 500px;
    width: 500px;
    background-color: #ffffff;
    margin: 100px auto;
}
.appModalFoodCategory__header
{
    height: 70px;
    border:1px solid #EFF2F4;
    @include flex-container(1)
}
.appModalFoodCategory__header--question,.appModalFoodCategory__header--close
{
    cursor: pointer;
}
.header__left
{
    height: 70px;
    width: 50%;
    @include flex-container(0)
}
.header__right
{
    height: 70px;
    width: 55px;
    @include flex-container(1)
}
.appModalFoodCategory__header--question,
.appModalFoodCategory__header--close
{
    width: 24px;
    height: 24px;
    border: 1px solid #A0B1BA;
    border-radius: 50%;
    @include flex-container(2)
}
.appModalFoodCategory__content
{
    height: calc(500px - 140px);
    border: 1px solid #A0B1BA;
    overflow-y:auto;
    padding-top:14px ;
}
.content__row
{
    height: 70px;
    margin-bottom: 14px;
    display: flex;
    flex-direction: column;
}

.appModalFoodCategory__footer
{
    height: 70px;
    @include flex-container(1)
}
</style>