<template>
  <div class="employee">
    <div class="employee__title">
        <h3 class="title--name">Nhân viên</h3>
        <button @click="OpenAppModal()" class="btn btn-outline-success title--add">
            Thêm nhân viên
        </button>
    </div>
    <div class="employee__data bg-light">
            <div class="data__container-1">
                <div class="record--quantity--page">
                    <i class="text-muted">Hiển thị :</i>
                    <select class="form-control">
                      <option selected>10</option>
                      <option value="1">25</option>
                      <option value="2">50</option>
                      <option value="3">100</option>
                    </select>
                    <i class="text-muted">bản ghi</i>
                </div>
                <div class="record--search">
                    <i class="text-muted">Tìm kiếm :</i>
                    <input type="text" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
                </div>
            </div>
            <div class="data__container-2 table-responsive">
                <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                                    <thead>
                                        <tr>
                                            <th>
                                                <input type="checkbox" aria-label="Checkbox for following text input">
                                            </th>
                                            <th>Tên nhân viên</th>
                                            <th>Ngày sinh</th>
                                            <th>Giới tính</th>
                                            <th>Số điện thoại</th>
                                            <th>Địa chỉ</th>
                                            <th>Email</th>
                                            <th>Tài khoản ngân hàng</th>
                                            <th>Tên ngân hàng</th>
                                            <th>Số chứng minh nhân dân</th>

                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(employee,index) in employeeList" :key="index">
                                            <td>
                                                <div class="column--val">
                                                    <input type="checkbox" aria-label="Checkbox for following text input">
                                                </div>
                                            </td>
                                            
                                            <td>
                                                <div class="column--val">
                                                    {{employee.employeeName}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{formatDateTime(employee.dateOfBirth)}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{formatGender(employee.employeeSex)}}
                                                </div>    
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                    {{employee.phoneNumber}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                        {{employee.employeeAddress}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                      {{employee.employeeEmail}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                        {{employee.bankAccount}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                       {{employee.bankName}}
                                                </div>
                                            </td>
                                            <td>
                                                <div class="column--val">
                                                       {{employee.identityCard}}
                                                </div>
                                            </td>
                                            <td class="feature-column">
                                                <div class="column--val">
                                                <button @click="OpenAppModal(employee)" class="btn btn-primary btn-fix mr-2">
                                                    Sửa
                                                 </button>
                                                 <button @click="deleteOneEmployee(employee.employeeID)" class="btn btn-danger btn-deleteOne">
                                                    Xóa
                                                 </button>
                                                </div>
                                            </td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
            </div>
            <div class="data__container-3">
                <div class="record--quantity--count text-muted">
                    <i>Hiển thị 1 to 10 of 12 records</i> 
                </div>
                <div class="record--pagination ">
                    <div class="page">Previous</div>
                    <div class="page">1</div>
                    <div class="page">2</div>
                    <div class="page">3</div>
                    <div class="page">4</div>
                    <div class="page">5</div>
                    <div class="page">6</div>
                    <div class="page">Next</div>
                </div>
            </div>
    </div>
  </div>
    <app-modal-employee
    v-if="isOpen"
    :isClose="CloseAppModal"
    :employeeData="employeeData"
    />
</template>

<script>
import moment from 'moment'
import {filterGender} from '@/store/format'
import appModalEmployee from './AppModalEmployee.vue';
import { createNamespacedHelpers } from 'vuex';
const {mapState,mapActions} =createNamespacedHelpers("employee")
export default {
    data(){
        return{
            isOpen:false,
            employeeData:""
        }
    },
    components:{
        appModalEmployee
    },
    computed:{
        ...mapState({
            employeeList:"employeeList"
        })
    },
    methods:{
        formatDateTime(value)
        {
            if(value)
            {
                return moment(String(value)).format('DD-MM-YYYY');
            }
        },
        formatGender(val)
        {
            return filterGender(val)
        },
        OpenAppModal(payload){
            if(payload==null)
            {
                this.isOpen=true
                this.employeeData=""
            }
            else
            {
                this.isOpen=true
                this.employeeData=payload
            }
        },
        CloseAppModal(){
            this.isOpen=false
        },
        ...mapActions({
            getAllEmployee:"getAllEmployee",
            deleteOneEmployee:"deleteOneEmployee"
        })
    },
    created(){
        this.getAllEmployee();
    }
};
</script>

<style lang="scss" scoped>
@mixin flex-container($val) {
  display: flex;
  align-items: center;
  @if $val == 1 {
    justify-content: space-between;
  } @else if $val==2 {
    justify-content: center;
  } @else if $val==3 {
    justify-content: end;
  }
}
.employee__title
{
    height: 80px;
    @include flex-container(1)
}
.employee__data
{
    padding: 0px 14px;
    border: 1px solid #DCF1D8;
    box-shadow:0 4px 10px 2px rgba(0, 0, 0, 0.2);
}
input[type="checkbox"]
{
    height: 16px;
    width: 16px;
}
input[type="checkbox"]:checked
{
    accent-color: green;
}
.data__container-1,.data__container-3
{
    height: 50px;
    @include flex-container(1)
}

.record--quantity--page
{
   width: 50%;
   @include flex-container(0)
}
.custom-select
{
   width: 70px;
}
.record--search
{
   width: 50%;
   @include flex-container(3)
}
.form-control
{
    width: 200px;
}
.feature-column
{
    @include flex-container(1)
}
.record--quantity--count
{
    width: 50%;
    @include flex-container(0)
}
.record--pagination
{
    width: 50%;
    @include flex-container(3)
}
.record--pagination .page
{
    border: 1px solid #9CD5B3;
    padding: 14px;
    height: 40px;
    cursor: pointer;
    @include flex-container(0)
}
.record--pagination .page:hover
{
    color: #ffffff;
    background-color: #067933;
}

.column--val
{
    @include flex-container(2)

}

</style>