<template>
    <the-header />

    <!-- Page Header Start -->
    <div class="container-fluid page-header mb-5 wow fadeIn" data-wow-delay="0.1s">
        <div class="container">
            <h1 class="display-3 mb-3 animated slideInDown">Sản phẩm</h1>
            <nav aria-label="breadcrumb animated slideInDown">
                <ol class="breadcrumb mb-0">
                    <li class="breadcrumb-item"><a class="text-body" href="#">Trang chủ</a></li>
                    <li class="breadcrumb-item text-dark active" aria-current="page">Chi tiết sản phẩm</li>
                </ol>
            </nav>
        </div>
    </div>
    <!-- Page Header End -->

    <div class="container container-detail d-flex">
        <div class="container__left text-center">
            <div class="container--image">
                <img class="image-main img-thumbnail" :src="foodDetail.foodImage" alt="">
            </div>
            <div class="container-related--image d-flex mt-5 px-5">
                <div class="image-sub">
                    <img src="" alt="">
                </div>
                <div class="image-sub">
                    <img src="" alt="">
                </div>
                <div class="image-sub">
                    <img src="" alt="">
                </div>
                <div class="image-sub">
                    <img src="" alt="">
                </div>
            </div>
        </div>
        <div class="container__right px-5 py-3">
            <h1 class="detail--name fw-bolder">{{foodDetail.foodName}}</h1>
            <div class="detail--price d-flex mb-3">
                <h4 class="text-danger me-5">{{formatMoney(foodDetail.foodPrice)}}</h4>
                <h4 class="text-success">Sale ({{foodDetail.foodSale}})%</h4>
            </div>
            <div class="detail--quantity mb-5">
                <p class="title fw-bolder">Số lượng hiện có</p>
                <input disabled  type="text" :value="foodDetail.foodQuantity" class="quantity form-control">
            </div>
            <div class="detail--btn d-flex flex-column mb-5">
                <div @click="addToCart({...foodDetail,bookingQuantity})" class="btn btn-light fw-bolder mb-3 border ">
                    THÊM VÀO GIỎ HÀNG
                </div>
                <div class="btn btn-dark fw-bolder">
                    MUA NGAY
                </div>
            </div>
            <div class="detail--share d-flex">
                <div class="icon border p-2 me-2">
                    <i class="fa-brands fa-facebook me-1 text-info"></i>
                    <b>Facebook</b>
                </div>
                <div class="icon border p-2 me-2">
                    <i class="fa-brands fa-twitter me-1 text-info"></i>
                    <b>Twitter</b>
                </div>
                <div class="icon border p-2 me-2">
                    <i class="fa-brands fa-instagram me-1 orange-400"></i>
                    <b>Instagram</b>
                </div>
                <div class="icon border p-2 me-2">
                    <i class="fa-regular fa-at me-1 gray-600"></i>
                    <b>Email</b>
                </div>
                <p>--> Share</p>
            </div>
        </div>
    </div>
    
    <the-footer/>
</template>
<script>
import TheHeader from '@/components/Client/TheHeader.vue'
import TheFooter from '@/components/Client/TheFooter.vue'
import {mapState,mapMutations,mapActions} from "vuex"
import {formatter} from "@/store/format"
export default {
    components:{
        TheHeader,
        TheFooter
    },
    data(){
        return{
            bookingQuantity:1
        }
    },
    computed:{
        ...mapState("food",{
            foodDetail:"foodDetail"
        }),
    },
    methods:{
        formatMoney(val)
        {
            return formatter.format(val)
        },
        ...mapMutations("cart",{
            addToCart:"addToCart"
        }),
        ...mapActions("food",{
            getOneFood:"getOneFood"
        })
    },
    created(){
        this.getOneFood(this.$route.params.id)
    }
}
</script>
    
<style scoped>
.container-detail
{
    /* background-color: #CEEAD9; */
    padding: 0px 50px;
}
.container__left,.container__right
{
    width: 50%;
    /* height: 600px; */
    border: 1px solid black;
}
.container-related--image .image-sub
{
    width: 100px;
    height: 100px;
    background-color: #CEEAD9;
    margin: 0px 14px;
}
.detail--quantity .quantity
{
    width: 70px;
}

</style>