<template>
  <the-header />

  <!-- Page Header Start -->
  <div
    class="container-fluid page-header mb-5 wow fadeIn"
    data-wow-delay="0.1s"
  >
    <div class="container">
      <h1 class="display-3 mb-3 animated slideInDown">ĐẶT HÀNG</h1>
      <nav aria-label="breadcrumb animated slideInDown">
        <ol class="breadcrumb mb-0">
          <li class="breadcrumb-item">
            <a class="text-body" href="#">Trang chủ / Giỏ hàng</a>
          </li>
          <li
            class="breadcrumb-item text-dark active fw-bolder"
            aria-current="page"
          >
            Đặt hàng
          </li>
        </ol>
      </nav>
    </div>
  </div>
  <!-- Page Header End -->

  <!-- DEMO HTML -->
  <div class="container mt-5">
    <div class="row">
      <div class="col-md-6 order-md-2 mt-1 mb-4">
        <h4 class="d-flex justify-content-between align-items-center mb-3">
          <span class="text-muted">Giỏ hàng của bạn</span>
          <span class="badge badge-secondary badge-pill">3</span>
        </h4>
        <ul class="list-group mb-3">
          <li
            class="list-group-item d-flex justify-content-between lh-condensed"
            v-for="(food, index) in bookingFoodList"
            :key="index"
          >
            <div>
              <h4 class="my-0">{{ food.foodName }}</h4>
              <b class="text-dark me-4"
                >Gía : {{ formatMoney(food.foodPrice) }}</b
              >
              <b class="text-dark">Sale ({{ food.foodSale }})%</b>
              <p class="text-dark">Số lượng đặt : {{ food.bookingQuantity }}</p>
            </div>
            <img
              style="width: 20%"
              class="img-thumbnail"
              :src="food.foodImage"
              alt=""
            />
            <span class="text-muted">{{
              priceTotalFood(
                food.foodPrice,
                food.foodSale,
                food.bookingQuantity
              )
            }}</span>
          </li>

          <li class="list-group-item d-flex justify-content-between">
            <span>Tổng trị giá : (VND)</span>
            <strong>{{ formatMoney(priceTotalCart) }}</strong>
          </li>
        </ul>
      </div>
      <div class="col-md-6 order-md-1">
        <h4 class="mb-3">THÔNG TIN THANH TOÁN</h4>
        <form  class="needs-validation" novalidate>
          <div class="row">
            <div class="col-md-3 mb-3">
              <label for="saleBill_Code">Mã hoá đơn</label>
              <input
                type="text"
                class="form-control"
                id="saleBill_Code"
                v-model="saleBill.saleBill_Code"
                disabled
              />
            </div>
          </div>

          <div class="mb-3 col-md-8">
            <label for="username">Họ và tên</label>
            <div class="input-group">
              <input
                type="text"
                class="form-control"
                id="username"
                v-model="saleBill.customerName"
                required
              />
              <div class="invalid-feedback" style="width: 100%">
                Xin vui lòng nhập tên của bạn
              </div>
            </div>
          </div>
          <div class="mb-3 col-md-8">
            <label for="phoneNumber">Số điện thoại</label>
            <input
              type="text"
              class="form-control"
              v-model="saleBill.phoneNumber"
              id="phoneNumber"
            />
            <div class="invalid-feedback">
              Xin vui lòng nhập số điện thoại của bạn
            </div>
          </div>

          <div class="mb-3 col-md-8">
            <label for="address">Địa chỉ</label>
            <input
              type="text"
              class="form-control"
              v-model="saleBill.customerAddress"
              id="address"
              required
            />
            <div class="invalid-feedback">
              Xin vui lòng nhập địa chỉ của bạn
            </div>
          </div>
          <hr class="mb-4" />
          <button
            @click="addOneSaleBill(saleBill)"
            class="btn btn-primary btn-lg btn-block"
            type="submit"
          >
            ĐẶT HÀNG NGAY
          </button>
        </form>
      </div>
    </div>
  </div>
  <!-- End Demo HTML -->

  <the-footer />
</template>

<script>
import TheHeader from "@/components/Client/TheHeader.vue";
import TheFooter from "@/components/Client/TheFooter.vue";
import { formatter } from "@/store/format";
import { getFoodPriceTotal } from "@/store/calculator";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    TheHeader,
    TheFooter,
  },
  data() {
    return {
      saleBill: {
        saleBill_ID: "05c10da5-16f2-4a76-baef-3239c65a1df0",
        saleBill_Code: "",
        customerName: "",
        phoneNumber: "",
        customerAddress: "",
        saleBill_Status: 0,
        priceTotal: 0,
        createDay:"2001-09-12",
        createBy:"Đỗ Đình Tỉnh",
        modifiedDay:"2001-09-12",
        modifiedBy:"Đỗ Đình Tỉnh",
        detail_SB:[]
      },
    };
  },
  computed: {
    ...mapState("cart", {
      bookingFoodList: "bookingFoodList",
      priceTotalCart: "priceTotalCart",
    }),
  },
  methods: {
    formatMoney(val) {
      return formatter.format(val);
    },
    priceTotalFood(foodPrice, foodSale, bookingQuantity) {
      var sum = getFoodPriceTotal(foodPrice, foodSale, bookingQuantity);
      return this.formatMoney(sum);
    },
    ...mapActions("saleBill", {
      getSaleBillCodeMax: "getSaleBillCodeMax",
      addOneSaleBill:"addOneSaleBill"
    }),
    
  },
  created() {
    this.getSaleBillCodeMax().then(
      (result) => (this.saleBill.saleBill_Code = result)
    );

    
    
  },

};
</script>

<style scoped>
</style>